/* ✅ Conteneur principal */
.admin-details-container {
    width: 90%; /* Largeur globale ajustable */
    max-width: 800px; /* Limite la largeur */
    margin: auto; /* Centre horizontalement */
    padding: 20px; /* Espace interne */
    
    /* 🔧 Permet d'ajuster les espaces gauche et droite */
    margin-left: auto;
    margin-right: auto;
}

/* ✅ Formulaire */
.admin-form {
    display: flex;
    flex-direction: column;
    align-items: center; /* Centre les champs */
    gap: 15px; /* Espacement vertical entre les champs */
    
    /* 🔧 Ajustement des espaces gauche et droite */
    padding-left: 30px; /* Modifie l’espace à gauche */
    padding-right: 30px; /* Modifie l’espace à droite */
}

/* ✅ Champs de saisie */
.form-group {
    width: 100%; /* Prend toute la largeur */
    display: flex;
    flex-direction: column;
    align-items: center; /* Centre les champs */
}

/* ✅ Champs de saisie (input) */
.form-group input, .form-group select {
    width: 60%; /* Prend toute la largeur du container */
    max-width: 400px; /* 🔧 Ajuste la largeur */
    padding: 10px;
    font-size: 20px; /* Modifie la taille des textes des champs */
    border: 1px solid #ccc;
    border-radius: 5px;
    text-align: center; /* Centre les textes des champs */

    /* 🔧 Ajuste les marges gauche/droite */
    margin-left: auto;
    margin-right: auto;
}

/* ✅ Labels */
.form-group label {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 5px;
}

/* ✅ Conteneur des boutons */
.button-container {
    display: flex;
    flex-direction: column; /* Boutons en colonne */
    align-items: center; /* Centre les boutons horizontalement */
    gap: 12px; /* Espacement entre les boutons */
    margin-top: 20px;
    width: 100%;
}

/* ✅ Boutons uniformisés */
.button-container button {
    width: 90%; /* Largeur uniforme */
    max-width: 250px; /* Ajustable */
    padding: 14px;
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
    text-align: center;
}

/* ✅ Bouton "Enregistrer" avec possibilité d'ajuster le centrage */
.btn-save {
    background-color: #28a745;
    color: white;

    /* 🔧 Ajustement manuel de la position gauche/droite */
    margin-left: auto; /* Ajuster vers la gauche */
    margin-right: auto; /* Ajuster vers la droite */
}

/* ✅ Styles pour les autres boutons */
.btn-delete {
    background-color: #dc3545;
    color: white;
}

.btn-history {
    background-color: #007bff;
    color: white;
}

.btn-cancel {
    background-color: #6c757d;
    color: white;
}

/* ✅ Effet au survol */
.button-container button:hover {
    opacity: 0.8;
}

/* ✅ Responsive : Ajustement pour petits écrans */
@media (max-width: 600px) {
    .button-container button {
        width: 100%;
        max-width: 300px;
    }
}